    </div>
    
    <!-- ÂÆûÁî®ÊèêÁ§∫ -->
    <div v-if="isModuleEnabled('tips')" class="card mb-4">
      <div class="card-header d-flex justify-content-between align-items-center">
        <h5 class="mb-0">üí° ÂÆûÁî®ÊèêÁ§∫</h5>
        <button class="btn btn-sm btn-light" @click="addTip">
