            </button>
          </div>
        </div>
        <p v-if="content.tips.length === 0" class="text-muted text-center py-3 mb-0">
          ÊöÇÊó†ÊèêÁ§∫ÔºåÁÇπÂáª‰∏äÊñπ"Ê∑ªÂä†"ÊåâÈíÆ
        </p>
      </div>
