</template>

<script>
import { computed } from 'vue'

export default {
  name: 'ContentEditor',
  
  props: {
    modelValue: {
      type: Object,
      required: true
    },
    enabledModules: {
      type: Array,
      default: () => []
    }
  },
  
  emits: ['update:modelValue'],
  
  setup(props, { emit }) {
    const content = computed({
      get: () => props.modelValue,
      set: (val) => emit('update:modelValue', val)
    })
    
    const isModuleEnabled = (moduleId) => {
      return props.enabledModules.includes(moduleId)
    }
    
    const budgetTotal = computed(() => {
      return content.value.budget.items.reduce((sum, item) => sum + (item.amount || 0), 0)
    })
    
    // ‰∫ÆÁÇπÁÆ°ÁêÜ
    const addHighlight = () => {
      content.value.highlights.push('')
    }
    
    const removeHighlight = (index) => {
      content.value.highlights.splice(index, 1)
    }
    
    // Ë°åÁ®ãÁÆ°ÁêÜ
    const addItinerary = () => {
      content.value.itinerary.push({
        day: '',
        time: '',
        content: '',
        highlight: ''
      })
    }
    
    const removeItinerary = (index) => {
      content.value.itinerary.splice(index, 1)
    }
    
    // È¢ÑÁÆóÁÆ°ÁêÜ
    const addBudgetItem = () => {
      content.value.budget.items.push({
        name: '',
        amount: 0,
        note: ''
      })
    }
    
    const removeBudgetItem = (index) => {
      content.value.budget.items.splice(index, 1)
    }
    
    // ÊèêÁ§∫ÁÆ°ÁêÜ
    const addTip = () => {
      content.value.tips.push('')
    }
    
    const removeTip = (index) => {
      content.value.tips.splice(index, 1)
    }
    
    return {
      content,
      isModuleEnabled,
      budgetTotal,
      addHighlight,
      removeHighlight,
      addItinerary,
      removeItinerary,
      addBudgetItem,
      removeBudgetItem,
      addTip,
      removeTip
    }
  }
}
</script>

