<template>
  <div class="editor-wrapper">
    <NavBar />
    
    <div class="editor-container">
      <!-- È°∂ÈÉ®Â∑•ÂÖ∑Ê†è -->
      <div class="editor-toolbar">
        <div class="container-fluid">
          <div class="d-flex justify-content-between align-items-center py-3">
            <div>
              <button class="btn btn-outline-secondary me-2" @click="goBack">
                <i class="bi bi-arrow-left me-1"></i>ËøîÂõû
              </button>
              <h5 class="d-inline-block mb-0 ms-3">
                {{ isEditMode ? 'ÁºñËæëÊóÖË°åËÆ°Âàí' : 'ÂàõÂª∫ÊóÖË°åËÆ°Âàí' }}
              </h5>
            </div>
            <div>
              <button 
                class="btn btn-outline-primary me-2" 
                @click="handleSave"
                :disabled="saving"
              >
                <span v-if="saving" class="spinner-border spinner-border-sm me-1"></span>
                <i v-else class="bi bi-save me-1"></i>
                ‰øùÂ≠òËçâÁ®ø
              </button>
              <button 
                class="btn btn-primary" 
                @click="handlePublish"
                :disabled="publishing || !canPublish"
              >
                <span v-if="publishing" class="spinner-border spinner-border-sm me-1"></span>
                <i v-else class="bi bi-send me-1"></i>
                ÂèëÂ∏É
              </button>
            </div>
          </div>
        </div>
      </div>
      
      <!-- ‰∏ªÁºñËæëÂå∫ -->
      <div class="container py-4">
        <div class="row">
          <!-- Â∑¶‰æßÔºöÁºñËæëÈù¢Êùø -->
          <div class="col-lg-8">
            <!-- Âü∫Êú¨‰ø°ÊÅØÁºñËæëÂô® -->
            <BasicInfoEditor v-model="tripData" />
            
            <!-- Ê®°ÂùóÈÄâÊã©Âô® -->
            <ModuleSelector
              :modules="availableModules"
              :enabled-modules="tripData.config.enabledModules"
              @toggle="toggleModule"
            />
            
            <!-- ÂÜÖÂÆπÁºñËæëÂô® -->
            <ContentEditor
              v-model="tripData.overview"
              :enabled-modules="tripData.config.enabledModules"
            />
          </div>
          
          <!-- Âè≥‰æßÔºöËÆæÁΩÆÈù¢Êùø -->
          <div class="col-lg-4">
            <EditorSidebar
              v-model="tripData"
              :days-count="daysCount"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useUserStore } from '@/stores'
import { getTripPlan, createTripPlan, updateTripPlan } from '@/api/tripPlan'
import NavBar from '@/components/NavBar.vue'
import BasicInfoEditor from '@/components/editor/BasicInfoEditor.vue'
import ModuleSelector from '@/components/editor/ModuleSelector.vue'
import ContentEditor from '@/components/editor/ContentEditor.vue'
import EditorSidebar from '@/components/editor/EditorSidebar.vue'

export default {
  name: 'TripEditorView',
  
  components: {
    NavBar,
    BasicInfoEditor,
    ModuleSelector,
    ContentEditor,
    EditorSidebar
  },
  
  setup() {
    const route = useRoute()
    const router = useRouter()
    const userStore = useUserStore()
    
    const saving = ref(false)
    const publishing = ref(false)
    
    // ÂèØÁî®Ê®°Âùó
    const availableModules = [
      { id: 'basicInfo', name: 'Âü∫Êú¨‰ø°ÊÅØ', icon: '‚ÑπÔ∏è', description: 'Âá∫ÂèëÂú∞„ÄÅÁõÆÁöÑÂú∞Á≠â' },
      { id: 'highlights', name: 'Ë°åÁ®ã‰∫ÆÁÇπ', icon: '‚ú®', description: '‰∏ªË¶ÅÊôØÁÇπÂíåÊ¥ªÂä®' },
      { id: 'itinerary', name: 'ËØ¶ÁªÜË°åÁ®ã', icon: 'üìÖ', description: 'ÊØèÊó•ÂÆâÊéí' },
      { id: 'budget', name: 'È¢ÑÁÆóÂèÇËÄÉ', icon: 'üí∞', description: 'Ë¥πÁî®ÊòéÁªÜ' },
      { id: 'tips', name: 'ÂÆûÁî®ÊèêÁ§∫', icon: 'üí°', description: 'Ê≥®ÊÑè‰∫ãÈ°π' },
    ]
    
    // ÊóÖË°åÊï∞ÊçÆ
    const tripData = ref({
      title: '',
      description: '',
      icon: 'üó∫Ô∏è',
      start_date: null,
      end_date: null,
      status: 'draft',
      visibility: 'private',
      theme_color: '#f0e68c',
      background_music: '',
      config: {
        enabledModules: ['basicInfo', 'highlights']
      },
      overview: {
        basicInfo: {
          departure: '',
          destination: '',
          transport: '',
          accommodation: ''
        },
        highlights: [],
        itinerary: [],
        budget: {
          items: [],
          total: 0
        },
        tips: []
      },
      created_at: null,
      updated_at: null
    })
    
    const isEditMode = computed(() => {
      const slug = route.params.slug
      return slug && slug !== 'new'
    })
    
    const canPublish = computed(() => {
      return tripData.value.title && tripData.value.title.trim().length > 0
    })
    
    const daysCount = computed(() => {
      if (!tripData.value.start_date || !tripData.value.end_date) return 0
      const start = new Date(tripData.value.start_date)
      const end = new Date(tripData.value.end_date)
      return Math.ceil((end - start) / (1000 * 60 * 60 * 24)) + 1
    })
    
    // Ê®°ÂùóÁÆ°ÁêÜ
    const toggleModule = (moduleId) => {
      const index = tripData.value.config.enabledModules.indexOf(moduleId)
      if (index > -1) {
        tripData.value.config.enabledModules.splice(index, 1)
      } else {
        tripData.value.config.enabledModules.push(moduleId)
      }
    }
    
    // ‰øùÂ≠ò
    const handleSave = async () => {
      if (!canPublish.value) {
        alert('ËØ∑Ëá≥Â∞ëÂ°´ÂÜôÊ†áÈ¢ò')
        return
      }
      
      saving.value = true
      try {
        // ËÆ°ÁÆóÈ¢ÑÁÆóÊÄªËÆ°
        tripData.value.overview.budget.total = tripData.value.overview.budget.items.reduce(
          (sum, item) => sum + (item.amount || 0), 0
        )
        
        if (isEditMode.value) {
          await updateTripPlan(route.params.slug, tripData.value)
          alert('‰øùÂ≠òÊàêÂäüÔºÅ')
        } else {
          const result = await createTripPlan(tripData.value)
          alert('ÂàõÂª∫ÊàêÂäüÔºÅ')
          router.push(`/editor/${result.slug}`)
        }
      } catch (error) {
        console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
        alert('‰øùÂ≠òÂ§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      } finally {
        saving.value = false
      }
    }
    
    // ÂèëÂ∏É
    const handlePublish = async () => {
      if (!canPublish.value) {
        alert('ËØ∑Ëá≥Â∞ëÂ°´ÂÜôÊ†áÈ¢ò')
        return
      }
      
      if (!confirm('Á°ÆÂÆöË¶ÅÂèëÂ∏ÉËøô‰∏™ÊóÖË°åËÆ°ÂàíÂêóÔºüÂèëÂ∏ÉÂêéÂ∞ÜÂèØ‰ª•Ë¢´ÂÖ∂‰ªñÁî®Êà∑ÁúãÂà∞„ÄÇ')) {
        return
      }
      
      publishing.value = true
      try {
        tripData.value.status = 'published'
        tripData.value.overview.budget.total = tripData.value.overview.budget.items.reduce(
          (sum, item) => sum + (item.amount || 0), 0
        )
        
        if (isEditMode.value) {
          await updateTripPlan(route.params.slug, tripData.value)
        } else {
          const result = await createTripPlan(tripData.value)
          router.push(`/editor/${result.slug}`)
        }
        
        alert('ÂèëÂ∏ÉÊàêÂäüÔºÅ')
      } catch (error) {
        console.error('ÂèëÂ∏ÉÂ§±Ë¥•:', error)
        alert('ÂèëÂ∏ÉÂ§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      } finally {
        publishing.value = false
      }
    }
    
    const goBack = () => {
      if (confirm('Á°ÆÂÆöË¶ÅÁ¶ªÂºÄÂêóÔºüÊú™‰øùÂ≠òÁöÑÊõ¥ÊîπÂ∞Ü‰∏¢Â§±„ÄÇ')) {
        router.push('/my-trips')
      }
    }
    
    // Âä†ËΩΩÊï∞ÊçÆ
    const loadTripData = async () => {
      const slug = route.params.slug
      
      // Â¶ÇÊûúÊòØÊñ∞Âª∫Ê®°ÂºèÔºå‰∏çÂä†ËΩΩ
      if (!slug || slug === 'new') {
        return
      }
      
      // ÁºñËæëÊ®°ÂºèÔºöÂä†ËΩΩÁé∞ÊúâÊï∞ÊçÆ
      try {
        const data = await getTripPlan(slug)
        tripData.value = {
          ...tripData.value,
          ...data,
          // Á°Æ‰øùoverviewÂíåconfigÊúâÈªòËÆ§ÂÄº
          overview: data.overview || tripData.value.overview,
          config: data.config || tripData.value.config
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂ§±Ë¥•:', error)
        alert('Âä†ËΩΩÊóÖË°åËÆ°ÂàíÂ§±Ë¥•')
        router.push('/my-trips')
      }
    }
    
    onMounted(() => {
      if (!userStore.isLoggedIn) {
        alert('ËØ∑ÂÖàÁôªÂΩï')
        router.push('/login')
        return
      }
      loadTripData()
    })
    
    return {
      tripData,
      saving,
      publishing,
      isEditMode,
      canPublish,
      daysCount,
      availableModules,
      toggleModule,
      handleSave,
      handlePublish,
      goBack
    }
  }
}
</script>

<style scoped>
.editor-wrapper {
  min-height: 100vh;
  background: #f5f7fa;
}

.editor-container {
  padding-top: 60px;
}

.editor-toolbar {
  position: fixed;
  top: 60px;
  left: 0;
  right: 0;
  background: white;
  border-bottom: 1px solid #e0e0e0;
  z-index: 1000;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.edit-panel :deep(.card) {
  border: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  border-radius: 12px;
  overflow: hidden;
}

/* ÂìçÂ∫îÂºè */
@media (max-width: 991px) {
  .editor-container {
    padding-top: 120px;
  }
}
</style>
